<template>
    <div class="rating" v-if="rating">
        <div v-if="full === 0">
            <span>暂无评分</span>
        </div>
        <div v-else>
            <template v-for="n in full">
                <span class="star-full"></span>
            </template>
            <template v-for="n in half">
                <span class="star-half"></span>
            </template>
            <template v-for="n in gray">
                <span class="star-gray"></span>
            </template>
            <span :class="rating.average | getAverageColor">{{rating.average}}</span>
        </div>
        <slot name="ratingsCount"></slot>
    </div>
</template>

<script>
export default {
    name: "rating",
    props: {
        rating: {
            type: Object,
            required: true
        }
    },
    data() {
        return {
            full: 0,
            half: 0,
            gray: 0
        };
    },
    created() {
        let average = this.rating.average;
        this.full = parseInt(average / 2);
        this.half = average % 2 === 0 ? 0 : 1;
        this.gray = 5 - this.full - this.half;
    },
    filters: {
        getAverageColor: function(value) {
            console.log("ssssssss" + value);
            if (value > 6) {
                value = "average";
            } else {
                value = "average1";
            }
            return value;
        }
    }
};
</script>

<style lang="scss" scoped>
.rating {
    margin-top: 0.2rem;
    font-size: 18px;
    color: #aaa;
    display: flex;
}

.star-full {
    display: inline-block;
    width: 0.5rem;
    height: 0.5rem;
    background-image: url(data:image/png;base64,);
    background-size: 0.5rem 0.5rem;
}

.star-gray {
    display: inline-block;
    width: 0.5rem;
    height: 0.5rem;
    background-image: url(data:image/png;base64,);
    background-size: 0.5rem 0.5rem;
}

.star-half {
    display: inline-block;
    width: 0.5rem;
    height: 0.5rem;
    background-image: url(data:image/png;base64,);
    background-size: 0.5rem 0.5rem;
}

.average {
    padding-right: 0.8rem;
    color: rgb(22, 211, 22);
}
.average1 {
    color: rgb(211, 22, 22);
}
</style>



